import { Component, OnInit, ViewChild } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { MatPaginator } from '@angular/material/paginator';
import { MatSort } from '@angular/material/sort';
import { MatTable, MatTableDataSource } from '@angular/material/table';
import { UserRole } from '@models';
import { ApiService, SweetAlertService } from '@services';
import { COMMON_FUNCTION, ENDPOINTS, ROLES, STATUSES } from '@shared';

@Component({
  selector: 'app-user-role',
  templateUrl: './user-role.component.html',
  styleUrls: ['./user-role.component.scss']
})
export class UserRoleComponent implements OnInit {

  @ViewChild(MatPaginator, { static: false }) paginator: MatPaginator;
  @ViewChild(MatSort, { static: false }) sort: MatSort;
  @ViewChild(MatTable, { static: false }) table: MatTable<UserRole>;
  dataSource: MatTableDataSource<UserRole>;
  displayedColumns: string[] = ['name', 'status'];
  statusList = [...STATUSES];
  roleList = [...ROLES];

  userRole = new UserRole();
  userRoleList = [];

  userRoleForm: FormGroup;

  name: any; // in html but not existing in component -- encountered during "ng serve --aot"

  constructor(
    private apiService: ApiService,
    private formBuilder: FormBuilder,
    private sweetAlertService: SweetAlertService
  ) { }

  ngOnInit(): void {
    this.getList();
    this.setFormValidator();
  }

  getList(): void {
    this.apiService.findAll(ENDPOINTS.roles).subscribe((res: any) => {
      console.log(res.responseData.data);

      this.userRoleList = res.responseData.data;
      this.dataSource = new MatTableDataSource<UserRole>(this.userRoleList);
      this.dataSource.paginator = this.paginator;
      this.dataSource.sort = this.sort;
    }, (err: any) => {
    });
  }

  save(): void {
    this.apiService.save(ENDPOINTS.roles, this.userRole).subscribe(
      (res: any) => {
        if (res !== undefined) {
          this.sweetAlertService.success(res);
        }
      }, (err: any) => {
        this.sweetAlertService.error(err);
      }, () => {
        // actions after subscription
        this.resetFormValidator();
        this.getList();
      });
  }

  getUserRole(name): void {
    this.apiService.findAll(ENDPOINTS.roles+'/role-name/'+ name).subscribe((res: any) => {
      if (res) {
        this.userRole = res;
      }
    }, (err: any) => {
      this.userRole = err.error;
    });
  }

  update(): void {
    this.apiService.update(ENDPOINTS.roles, this.userRole).subscribe(
      (res: any) => {
        if (res !== undefined) {
          this.sweetAlertService.success(res);
        }
      }, (err: any) => {
        this.sweetAlertService.error(err);
      }, () => {
        // actions after subscription
        this.resetFormValidator();
        this.getList();
      });
  }

  setFormValidator(): void {
    this.userRole.status = COMMON_FUNCTION.ACTIVE;
    this.userRoleForm = this.formBuilder.group({
      name: ['', Validators.required],
      status: [''],
    });
  }

  resetFormValidator(): void {
    this.userRole = new UserRole();
    this.userRole.status = COMMON_FUNCTION.ACTIVE;
    this.userRoleForm.markAsUntouched();
    this.userRoleForm.markAsPristine();
  }

}
